// Splash Screen
$ani-time-splash-step1:var(--ani-time-splash-step-1);
$ani-time-splash-step2:var(--ani-time-splash-step-2);
$ani-time-splash-step3:var(--ani-time-splash-step-3);

// Basic Animations
@keyframes basic-fade-in {
    0%   { opacity: 0; }
    100% { opacity: 1; }
} 
@keyframes basic-fade-out {
    0%   { opacity: 1; }
    100% { opacity: 0; }
} 