@mixin globalHelpers() {
    // Global Helpers
    .align-center {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .force-nobreak {
        white-space: nowrap;
    }

    .a11y-hidden {
        @include a11y-hidden();
    }

    .hidden-desktop {
        @include desktopView() {
            display: none !important;
        }
    }

    .hidden-mobile {
        @include mobileView() {
            display: none !important;
        }
    }

    .visible-desktop {
        @include mobileView() {
            display: none !important;
        }
    }

    .visible-mobile {
        @include desktopView() {
            display: none !important;
        }
    }

    .color-main {
        color: $main;
    }

    .coming-soon-tex{
        white-space: nowrap;
        @media (max-width: 480px) {
            white-space: normal;
        }
    }

    .sec-info-ref{
        position: fixed;
        right:0;
        bottom:0;
        z-index: 10000;
        padding:3rem;
        background: rgba(#FFF, 0.5);
    }
}