@import "./styles/normalize.scss";
@import "./styles/AppDesign.scss";

html,
body {
  height: 100%;
}

html,
body,
#root {
  width: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}

main {
  width: 100%;
  overflow-x: hidden;
}

input,
button {
  outline: none;
  -webkit-appearance: none;
}

html {
  font-family: $fontPrimary;
  @include largeScreen() {
    font-size:0.8em;
  }
  @include tabletView() {
    font-size:0.8em;
  }
}

body {
  background: $lightGray;
  margin: 0;
  font-family: $fontPrimary;
  -moz-osx-font-smoothing: grayscale;
  color:$colorTxt;

  &:after{
       // load images
      position:absolute; width:0; height:0; overflow:hidden; z-index:-1; // hide images
      content:
        url(/img/splash/img-nwd-white.svg)
        url(/img/splash/img-nwd.svg)
        url(/img/splash/black/img-num-0.svg)
        url(/img/splash/black/img-num-1.svg)
        url(/img/splash/black/img-num-2.svg)
        url(/img/splash/black/img-num-3.svg)
        url(/img/splash/black/img-num-4.svg)
        url(/img/splash/black/img-num-5.svg)
        url(/img/splash/black/img-num-6.svg)
        url(/img/splash/black/img-num-7.svg)
        url(/img/splash/black/img-num-8.svg)
        url(/img/splash/black/img-num-9.svg)
        url(/img/splash/red/img-num-0.svg)
        url(/img/splash/red/img-num-1.svg)
        url(/img/splash/red/img-num-2.svg)
        url(/img/splash/red/img-num-3.svg)
        url(/img/splash/red/img-num-4.svg)
        url(/img/splash/red/img-num-5.svg)
        url(/img/splash/red/img-num-6.svg)
        url(/img/splash/red/img-num-7.svg)
        url(/img/splash/red/img-num-8.svg)
        url(/img/splash/red/img-num-9.svg)
        url(/img/splash/red/img-num-8.svg)
        url(/img/landing/bg-landing-raw.jpg)
        url(/img/landing/bg-landing.jpg)
        url(/img/landing/bg-landing-m.jpg)
      ;
  }
}

* {
  box-sizing: border-box;
}

@include globalHelpers();