@import "../../styles/AppDesign.scss";

.sand-test {
  display:block;
  padding: 60px 0;

  h2{
    margin: 0 0 10px;
  }
}

.sand-board{
  position: relative;
  // background:#000;
  width:100%;
  max-width:480px;
  margin:0 auto 30px;
  cursor: grab;
  padding-top:36px;

  .react-transform-component{
    background:#ccc;
  }

  .tools {
    position:absolute;
    width: 100%;
    top:5px;
    z-index: 150;
    background: transparent;
    text-align: right;
    button{
      margin-left: 3px;
      width:24px;
      height:24px;
      text-align: center;
      padding:0;
      border:2px solid #000;
    }
  }
}

.sand-msg{

}
.sand-msg-input{
  display:flex;
  align-items: center;
  justify-content: center;
  input{
    border: 0;
    border-bottom:2px solid #222;
    background: transparent;
    text-align: center;
    font-weight: 100;
    margin-right:5px;
  }
  button{
    border:2px solid #222;
    background: transparent;
  }
}
.sand-board__zoomer{
  position:relative;
  width:480px;
  &:before{
    content:'';
    display:block;
    padding-bottom:75%;
  }
}
.sand-board__body{
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  transform: scale(0.25, 0.25);
  transform-origin: left top;
}