
// Fonts Size (legacy):
// 20201226 - tried to use px for most type settings to be accurate
// Type settings in px can be found in the below of this file 
$fontSizeTitleXXL:10rem;
$fontSizeTitleXL:6.5rem;
$fontSizeTitleL:4rem;
$fontSizeTitleM:3rem;
$fontSizeTitleS:2.5rem;
$fontSizeTitleSS:1.3rem;
$fontSizeTitleXS:1rem;

$fontSizeXXL:2.5rem;
$fontSizeXL:2rem;
$fontSizeL:1.5rem;
$fontSizeM:1.3rem;
$fontSizeS:1rem;
$fontSizeSS:.9rem;
$fontSizeXS:.5rem;

// Font settings
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Regular", normal, 200);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Semibold", normal, 400);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Bold", normal, 600);


$fontPrimary: "Myriad Pro", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體,
sans-serif;

// Type settings
@mixin secTitleTypo() {
    // font-size:100px;
    font-size:90px;
    line-height: 1em;
    @include largeScreen(){
        // font-size:$fontSizeTitleL + 1.5;
        font-size:70px;
    }
    @include tabletView(){
        // font-size:$fontSizeTitleL;
        font-size:52px;
    }
    @include mobileView(){
        // font-size:$fontSizeTitleM;
        font-size:46px;
    }
}
@mixin secSubtitleTypo() {
    font-weight: 700;
    // font-size:20px;
    // line-height: 24px;
    font-size:28px;
    line-height: 40px;
    letter-spacing: 1px;
    @include smallScreen(){
        font-size:15px;
        line-height: 20px;
        letter-spacing: 1px;
    }
}

@mixin secRotatingTextTypo() {
    font-size:75px;
    @include largeScreen(){
        // font-size:$fontSizeTitleL + 1.5;
        font-size:60px;
    }
    @include tabletView(){
        // font-size:$fontSizeTitleL;
        font-size:42px;
    }
    @include mobileView(){
        // font-size:$fontSizeTitleM;
        font-size:40px;
    }
}
@mixin secDescTypo() {
    // font-size:$fontSizeM;
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 0;
    line-height: 34px;
    @include mobileView(){
        font-size: 18px;
        line-height: 26px;
    }
}


@mixin artisanDescTypo() {
    // font-size:$fontSizeM;
    font-weight: 400;
    font-size: 18px;
    letter-spacing: 0;
    line-height: 28px;
    @include mobileView(){
        font-size: 18px;
        line-height: 26px;
    }
}


@mixin secIndicatorTypo(){
    font-size: 15px;
    letter-spacing: 1px;
    line-height: 24px;
    @include mobileView(){
        font-size: 20px;
        letter-spacing: 1px;
        line-height: 24px;
    }
}
@mixin sidenavSocTypo(){
    font-size: 15px;
    letter-spacing: 1px;
    line-height: 24px;
}

@mixin specTypo(){
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 15px;
    line-height: 20px;
}

@mixin exhibitionTitleTypo {
    font-weight: 400;
    font-size: 40px;
    line-height: 44px;
    @include mobileView(){
        font-size: 30px;
        line-height: 36px;
    }
}
@mixin exhibitionDetailTypo {
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    @include mobileView(){
        font-size: 20px;
        line-height: 24px;
    }
}
@mixin exhibitinoQuote(){
    font-weight: 700;
    font-size: 28px;
    letter-spacing: 0;
    line-height: 40px;
    @include mobileView(){
        font-size: 26px;
        line-height: 32px;
    }
}

@mixin exhibitinoQuoteName(){
    font-weight: 700;
    font-size: 30px;
    color: #000000;
    letter-spacing: 1px;
    line-height: 35px;
    @include mobileView(){
        font-size: 24px;
        line-height: 34px;
    }
}

@mixin exhibitionQuoteTitle() {
    font-weight: 400;
    font-size: 20px;
    letter-spacing: 0;
    line-height: 28px;
}

@mixin sliderCaption(){
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
}

@mixin specBigTypo(){
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 18px;
    line-height: 28px;
}

@mixin specDescTypo(){
    font-weight: 400;
    letter-spacing: 1px;
    font-size: 15px;
    line-height: 20px;
}

@mixin footerTitleTypo(){
    font-weight: 700;
    font-size: 60px;
    letter-spacing: 0;
    line-height: 60px;
    @include mobileView(){
        font-size: 40px;
        line-height: 40px;
    }
}
@mixin footerSubtitleTypo(){
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 1px;
    line-height: 28px;
    @include mobileView(){
        font-size: 15px;
        line-height: 20px;
    }
}

@mixin footerCopyrightTypo(){
    font-weight: 400;
    font-size: 18px;
    letter-spacing: 0;
    line-height: 26px;
